import { useDispatch, useSelector } from "react-redux";

import { Container } from "shared/ui/container";
import { TSettings } from "entities/settings/types";
import { updateSettings } from "features/settings/slice/actions";
import { selectSettings } from "features/settings/slice/selectors";
import { Setting } from "features/settings/ui/setting";
import { SyncForm } from "features/sync/ui/syncForm";

import s from "./style.module.css";
import { selectUser } from "features/profile/slice/selectors";

export const SettingsPage = () => {
  const dispatch = useDispatch();
  const { detachSubitems = false } = useSelector(selectSettings);
  const user = useSelector(selectUser);

  const onUpdateSettings = (key: keyof TSettings, checked: boolean) => {
    dispatch(
      updateSettings({
        [key]: checked,
      })
    );
  };

  return (
    <div>
      <Container>
        <Setting
          isChecked={detachSubitems}
          onChange={(isChecked) =>
            onUpdateSettings("detachSubitems", isChecked)
          }
        >
              
        </Setting>

        <div className={s.Profile}>
          <h2> </h2>
          <p>
               ,    
             .
          </p>
          {user ? (
            <p>
                 ,   
                .
            </p>
          ) : (
            <p>   </p>
          )}

          <SyncForm />
        </div>
      </Container>
    </div>
  );
};
